import React, { Component } from 'react';
import axios from 'axios';

import WaveChart from '../WaveChart/WaveChart';

import './Timeline.css';

class Timeline extends Component {
    
  constructor( props ) {
    super(props);
      
    this.endpoint = 'https://india.elezioni.io/v0/timeline';
    
    this.load = this.load.bind( this );
    
    this.state = {
      articles: [],
      collections: {},
      loading: true,
      max: {}
    }
  }
  
  componentDidMount() {
    this.load();
  }
	
	load() {
	  axios.get( this.endpoint, { withCredentials: false } ).then(
	    ( response ) => {
	      if ( response.status === 200 && !response.data.error ) {
          console.log(response.data);
	        this.setState(
	          {
              articles: response.data.data.articles,
              collections: response.data.data.timeline.collections,
              loading: false,
              max: response.data.data.timeline.max
	          }
	        );
	      } else {
	        alert( 'An error occourred ' + response.statusText + ' ' + response.data.errormessage);
	      }
	    }
	  )
	  .catch(
	    ( error ) => {
	      alert( 'Error ' + error );
	      console.log( error );
	    }
	  );
	}
	
	
    
  render() {
    return (
      <div className="Timeline">

      <h1>Timeline Component</h1>
       
       <figure className="Timeline--chart">
        <div className="in">
          <div className="Timeline--wrapper">
            <WaveChart Series={this.state.collections} Articles={this.state.articles} Options={ { max: { y1: this.state.max.activity, y2: this.state.max.popularity }, y1: 'activity', y2: 'popularity' } } />
          </div>
        </div>
       </figure>
       
       <h2>{ ( this.state.loading ? 'loading' : 'loaded' ) }</h2>
       
      </div>
    );
  }
}

export default Timeline;
