import React, { Component } from 'react';
import './AreaChart.css';
import * as d3 from 'd3';

class AreaChart extends Component {
  constructor(props) {
    super(props);
    this.chart = React.createRef();

    this.draw = this.draw.bind( this );
    this.viewbox = this.viewbox.bind(this);

    this.state = {
      paths: {
        series: []
      },
      show: 'numbers',
      size: {
        h: 0,
        w: 0
      }
    };
  }

  componentDidMount() {
    window.addEventListener( 'resize', this.viewbox, false );
    this.viewbox();
  }

  componentDidUpdate( pProps, pState ) {
    if ( pProps !== this.props ) {
      this.viewbox();
    }
  }
  
  componentWillUnmount() {
		window.removeEventListener( 'resize', this.viewbox, false );
	}

  draw() {
    const coalitions = Object.keys( this.props.Series );
    // Find minX and maxX
	  const compareValues = (() => {
	    let result = [];
	    coalitions.forEach(
	      (coalition) => {
	        result.push( parseInt( this.props.Series[coalition][ 0 ].timestamp, 10 ) );
	        result.push( parseInt( this.props.Series[coalition][ (this.props.Series[coalition].length - 1) ].timestamp, 10 ) );
	      });
	    return result;
    })();
    // Define scaleX and scaleY
	  const topBaseY = 0;
    const bottomBaseY = this.state.size.h - 20;
    const scaleX = d3.scaleLinear()
      .domain([
        Math.min.apply(Math, compareValues ),
        Math.max.apply(Math, compareValues )
      ])
      .range( [ 0, this.state.size.w ] );
    
    const scaleY = (() => {
	    if ( this.props.Options.scale === 'numbers') {
	      return d3.scaleLinear()
          .domain( [
            0,
            this.props.Options.max.y
          ] )
          .range( [ bottomBaseY, topBaseY ] );
	    } else {
	      return d3.scaleLinear()
          .domain( [ 0, 100 ] )
          .range( [ bottomBaseY, topBaseY ] );
	    }
	  })();
	  
	  // Define area function
	  const area = d3.area()
      .x( points => points.x )
      .y1( points => points.y )
      .y0( points => bottomBaseY )
      .curve( d3.curveBasis );
      
    // Declare Basepoints and Points Objects 
    let basepoints = {};
    let basepointsPerc = {};
    let points = {};
    
    // Init Basepoint object
    coalitions.forEach(
      (coalition) => {
        basepoints[ coalition ] = {};
        basepointsPerc[ coalition ] = {};
        points[ coalition ] = [];
      });

    // Define SVG Areas
    const series = [];
    coalitions.forEach(
      (coalition, index) => {
        points[coalition] = [];
        this.props.Series[coalition].forEach(
          (day) => {
            basepoints[day.timestamp] = ( basepoints[day.timestamp] ) ? ( basepoints[day.timestamp] + parseInt( day[this.props.Options.y], 10) ) : parseInt( day[this.props.Options.y], 10);
            basepointsPerc[day.timestamp] = ( basepointsPerc[day.timestamp] ) ? ( basepointsPerc[day.timestamp] + parseInt( day[this.props.Options.y] || 0, 10) ) : parseInt( day[this.props.Options.y] || 0, 10);
            points[coalition].push({
              v: day[this.props.Options.y],
              t: parseInt( day.timestamp , 10 ),
              x: scaleX( parseInt( day.timestamp, 10 ) ),
              y: ( this.props.Options.scale === 'numbers') ? scaleY( basepoints[day.timestamp] ) : scaleY( basepointsPerc[day.timestamp] )
            });
          });
        series.push( {
          name: coalition,
          path: area( points[coalition] ),
          points: points[coalition]
        } )
      });
    series.reverse();
    this.setState( { loading: false, paths: { series: series } } );
	}

  viewbox() {
    let svgWrapper = this.chart.current;
    let size = {
      "h": svgWrapper.offsetHeight,
      "w": svgWrapper.offsetWidth,
    }
    this.setState(
      { size },
      () => {
        this.draw();
      }
    );
  }

  render() {
    return (
        <div className="AreaChart" ref={this.chart}>
          <svg
            viewport={"0 0 " + this.state.size.w + ' ' + this.state.size.h}
            preserveAspectRatio="xMidYMid meet"
            width={this.state.size.w}
            height={this.state.size.h}>
            <g>
              {
                this.state.paths.series.map(
                  (serie, index) => {
                    return (
                      <path
                        key={serie.name}
                        d={serie.path}
                        className={"area " + serie.name} />
                    );
                  }
                )
              }
            </g>
          </svg>
        </div>
    );
  }
}

export default AreaChart;
