import React, { Component } from 'react';
import './WaveChart.css';

import { createAbstract } from '../../Helpers/Helpers.js';
import * as moment from 'moment';
import * as d3 from 'd3';

class WaveChart extends Component {
  constructor(props) {
    super(props);
    this.chart = React.createRef();

    this.draw = this.draw.bind( this );
    this.viewbox = this.viewbox.bind(this);

    this.borders = 10;
    this.labelsDistance = 100;

    this.state = {
      paths: {
        articles: [],
        circles: [],
        labels: [],
        lines: [],
        series: []
      },
      show: 'numbers',
      size: {
        h: 0,
        w: 0
      }
    };
  }

  componentDidMount() {
    window.addEventListener( 'resize', this.viewbox, false );
  }

  componentDidUpdate( pProps, pState ) {
    if ( pProps !== this.props ) {
      this.viewbox();
    }
  }
  
  componentWillUnmount() {
		window.removeEventListener( 'resize', this.viewbox, false );
	}

  draw() {
    const coalitions = Object.keys( this.props.Series );
    // Find minX and maxX
	  const compareValues = (() => {
	    let result = [];
	    coalitions.forEach(
	      (coalition) => {
	        result.push( parseInt( this.props.Series[coalition][ 0 ].timestamp, 10 ) );
	        result.push( parseInt( this.props.Series[coalition][ (this.props.Series[coalition].length - 1) ].timestamp, 10 ) );
	      });
	    return result;
    })();
    // Define scaleX and scaleY
	  const scaleX = d3.scaleLinear()
      .domain([
        Math.min.apply(Math, compareValues ),
        Math.max.apply(Math, compareValues )
      ])
      .range( [ 0, this.state.size.w ] );
      
    // Declare Basepoints and Points Objects 
    let points = {};
    
    // Init Basepoint object
    
    // Define SVG Areas    
    const series = [];
    // Popularity
    coalitions.forEach(
      (coalition, index) => {
        points[coalition] = [];
        const availableHeight = Math.round( ( this.state.size.h ) / coalitions.length );
        const topLimit = ( index * availableHeight );
        
        const availableHeight2 = Math.round( ( this.state.size.h - ( this.borders * 2 ) ) / coalitions.length );
        const topLimit2 = this.borders + ( index * availableHeight2 );
        const zero = topLimit2 + Math.round( availableHeight2 / 2 );

        points[coalition] = [];
        const scaleY = d3.scaleLinear()
          .domain( [
            0,
            this.props.Options.max.y2
          ] )
          .range( [ zero, topLimit ] );

        // Define area function
        const area = d3.area()
          .x( points => points.x )
          .y1( points => points.y )
          .y0( points => zero )
          .curve( d3.curveBasis );
        
        this.props.Series[coalition].forEach(
          (day) => {
            points[coalition].push({
              v: day[this.props.Options.y2],
              t: parseInt( day.timestamp , 10 ),
              x: scaleX( parseInt( day.timestamp, 10 ) ),
              y: scaleY( day[this.props.Options.y2] )
            });
          });
        const loopingPoints = points[coalition].slice(0);
        for ( let i = (loopingPoints.length - 1); i >=0; i--) {
          let point = Object.assign({}, loopingPoints[i] );
          point.y = -1 * point.y;
          points[coalition].push({
            v: point.v,
            t: point.t,
            x: point.x,
            y: scaleY( point.v * -1 )
          });
        }
        series.push( {
          name: coalition + '-popularity',
          path: area( points[coalition] ),
          points: points[coalition]
        } )
      });
    // Activity
    coalitions.forEach(
      (coalition, index) => {
        const availableHeight = Math.round( ( this.state.size.h - ( this.borders * 2 ) ) / coalitions.length );
        const topLimit = this.borders + ( index * availableHeight );
        const zero = topLimit + Math.round( availableHeight / 2 );
        points[coalition] = [];
        const scaleY = d3.scaleLinear()
          .domain( [
            0,
            this.props.Options.max.y1
          ] )
          .range( [ zero, topLimit ] );

        // Define area function
        const area = d3.area()
          .x( points => points.x )
          .y1( points => points.y )
          .y0( points => zero )
          .curve( d3.curveBasis );
        
        this.props.Series[coalition].forEach(
          (day) => {
            points[coalition].push({
              v: day[this.props.Options.y1],
              t: parseInt( day.timestamp , 10 ),
              x: scaleX( parseInt( day.timestamp, 10 ) ),
              y: scaleY( day[this.props.Options.y1] )
            });
          });
        const loopingPoints = points[coalition].slice(0);
        for ( let i = (loopingPoints.length - 1); i >=0; i--) {
          let point = Object.assign({}, loopingPoints[i] );
          point.y = -1 * point.y;
          points[coalition].push({
            v: point.v,
            t: point.t,
            x: point.x,
            y: scaleY( point.v * -1 )
          });
        }
        series.push( {
          name: coalition,
          path: area( points[coalition] ),
          points: points[coalition]
        } )
      });
    // Lines and labels
    const lines = [];
    const labels = [];
    const articles = [];
    const circles = [];
    let prevX = 0;
    this.props.Articles.forEach(
      ( article ) => {
        let x = scaleX( parseInt( article.date_published_timestamp, 10 ) );
        let y1 = this.borders;
        let y2 = this.state.size.h  - this.borders;
        if ( ( ( lines.length === 0 ) || ( Math.abs( x - prevX ) >= this.labelsDistance ) ) && ( x < ( this.state.size.w - this.labelsDistance ) ) && ( x >= this.labelsDistance ) ) {
          lines.push({
            x1: x,
            x2: x,
            y1: y1 + 10,
            y2: y2
          });
          labels.push({
            label: moment( article.date_published).format("MMM Do"),
            x: x,
            y: y1
          });
          articles.push({
            source: article.source,
            tags: article.tags,
            title: article.title,
            url: article.url,
            x: x
          });
          circles.push({
            cx: x,
            cy: y2,
            r: 2
          });
          prevX = x;
        }
      });
    this.setState( { loading: false, paths: { articles, circles, labels, lines, series } } );
	}

  viewbox() {
    let svgWrapper = this.chart.current;
    let size = {
      "h": svgWrapper.offsetHeight,
      "w": svgWrapper.offsetWidth,
    }
    this.setState(
      { size },
      () => {
        this.draw();
      }
    );
  }

  render() {
    return (
        <div className="WaveChart">
            <div className="WaveChart--chart" ref={this.chart}>
              <svg
                viewport={"0 0 " + this.state.size.w + ' ' + this.state.size.h}
                preserveAspectRatio="xMidYMid meet"
                width={this.state.size.w}
                height={this.state.size.h}>
                <g className="series">
                  {
                    this.state.paths.series.map(
                      (serie, index) => {
                        return (
                          <path
                            key={serie.name}
                            d={serie.path}
                            className={"area " + serie.name} />
                        );
                      }
                    )
                  }
                </g>
                <g className="lines">
                  {
                    this.state.paths.lines.map(
                      (line, index) => {
                        return (
                          <line
                            key={index}
                            x1={line.x1}
                            x2={line.x2}
                            y1={line.y1}
                            y2={line.y2}
                            className="line" />
                        );
                      }
                    )
                  }
                </g>
                <g className="circles">
                  {
                    this.state.paths.circles.map(
                      (circle, index) => {
                        return (
                          <circle
                            key={index}
                            cx={circle.cx}
                            cy={circle.cy}
                            r={circle.r}
                            className="circle" />
                        );
                      }
                    )
                  }
                </g>
                <g className="labels">
                  {
                    this.state.paths.labels.map(
                      (label, index) => {
                        return (
                          <text
                            key={index}
                            x={label.x}
                            y={label.y}
                            textAnchor="middle"
                            alignmentBaseline="middle"
                            dominantBaseline="middle"
                            className="label">{label.label}</text>
                        );
                      }
                    )
                  }
                </g>
              </svg>
            </div>
            <div className="WaveChart--Articles">
              {
                this.state.paths.articles.map(
                  (article, index) => {
                    return (
                      <article
                        className="article"
                        key={index}
                        style={{ 'left': article.x + 'px' }}>
                        <a href={article.url} target="_article">{createAbstract( article.title )}</a>
                      </article>
                    );
                  }
                )
              }
            </div>
        </div>
    );
  }
}

export default WaveChart;
