import React, { Component } from 'react';
import axios from 'axios';

import StreamGraph from '../StreamGraph/StreamGraph';

import './OverviewSteamGraph.css';

class OverviewSteamGraph extends Component {
    
  constructor( props ) {
    super(props);
      
    this.endpoint = 'https://india.elezioni.io/v0/stats/history';
    
    this.load = this.load.bind( this );
    
    this.state = {
      coalitions: [],
      loading: true,
      max: {},
      series: []
    }
  }
  
  componentDidMount() {
    this.load();
  }
	
	load() {
	  axios.get( this.endpoint, { withCredentials: false } ).then(
	    ( response ) => {
	      if ( response.status === 200 && !response.data.error ) {
          const series = {
            activity: [],
            popularity: []
          };
          let coalitions = Object.keys( response.data.data.collections );
          coalitions.forEach(
            (coalition) => {
              response.data.data.collections[ coalition ].forEach(
                ( day, index ) => {
                  if ( series.activity[ index ]) {
                    series.activity[ index ][ coalition ] = day.activity;
                  } else {
                    let obj = {
                      day: day.timestamp
                    };
                    obj[coalition] = day.activity;
                    series.activity.push(obj);
                  }
                  if ( series.popularity[ index ]) {
                    series.popularity[ index ][ coalition ] = day.popularity;
                  } else {
                    let obj = {
                      day: day.timestamp
                    };
                    obj[coalition] = day.popularity;
                    series.popularity.push(obj);
                  }
                });
            });
          this.setState(
	          {
              coalitions,
              loading: false,
              max: response.data.data.max,
              series: series
            });
	      } else {
	        alert( 'An error occourred ' + response.statusText + ' ' + response.data.errormessage);
	      }
	    }
	  )
	  .catch(
	    ( error ) => {
	      alert( 'Error ' + error );
	      console.log( error );
	    }
	  );
	}
	
	
    
  render() {
    return (
      <div className="OverviewSteamGraph">

        <h1>OverviewSteamgraph Component</h1>
      
        <h3>StreamGraph Activity</h3>
        <figure className="OverviewSteamGraph--chart">
          <div className="in">
            <div className="OverviewSteamGraph--wrapper">
              <StreamGraph Data={this.state.series} Options={ { max: { y: this.state.max.activity }, show: 'activity', series: this.state.coalitions } } />
            </div>
          </div>
        </figure>

      
        <h3>StreamGraph Popularity</h3>
        <figure className="OverviewSteamGraph--chart">
          <div className="in">
            <div className="OverviewSteamGraph--wrapper">
              <StreamGraph Data={this.state.series} Options={ { max: { y: this.state.max.popularity }, show: 'popularity', series: this.state.coalitions } } />
            </div>
          </div>
        </figure>
        
        <h2>{ ( this.state.loading ? 'loading' : 'loaded' ) }</h2>
      
      </div>
    );
  }
}

export default OverviewSteamGraph;
