import React, { Component } from 'react';
import './LineChart.css';
import * as d3 from 'd3';
import * as moment from 'moment';

class LineChart extends Component {
  constructor(props) {
    super(props);
    this.chart = React.createRef();

    this.draw = this.draw.bind( this );
    this.viewbox = this.viewbox.bind(this);

    this.margins = [ 10, 0, 30, 0 ];
    this.labelsDistance = 40;

    this.state = {
      paths: {
        bullets: [],
        labels: [],
        lines: [],
        series: [],
        xAxis: {}
      },
      size: {
        h: 0,
        w: 0
      }
    };
  }

  componentDidMount() {
    window.addEventListener( 'resize', this.viewbox, false );
    this.viewbox();
  }

  componentDidUpdate( pProps, pState ) {
    if ( pProps !== this.props ) {
      this.viewbox();
    }
  }
  
  componentWillUnmount() {
		window.removeEventListener( 'resize', this.viewbox, false );
	}

  draw() {
    if ( this.props.Series && this.props.Series.length > 0 && this.props.Options ) {
      // Define scaleX and scaleY
      const topBaseY = this.margins[0];
      const bottomBaseY = this.state.size.h - this.margins[2];
      const leftBaseX = this.margins[3];
      const rightBaseX = this.state.size.w - this.margins[1];

      const scaleX = d3.scaleLinear()
        .domain(d3.extent( this.props.Series[ this.props.Options.index ].timeline.map( (child) => +child.timestamp )))
        .range( [ leftBaseX, rightBaseX ] );
      
      const scaleY = d3.scaleLinear()
        .domain( [ 0, this.props.Options.max[ this.props.Options.show ] ] )
        .range( [ bottomBaseY, topBaseY ] );
      
      const line = d3.line()
        .x( point => point.x )
        .y( point => point.y )
        .curve( d3.curveBasis );
      
      // Define SVG Areas
      const series = [];
      this.props.Series.forEach(
        (serie, index) => {
          const points = [];
          serie.timeline.forEach(
            ( day ) => {
              points.push({
                x: scaleX( day.timestamp ),
                y: scaleY( day[ this.props.Options.show ] )
              });
            }
          );
          series.push( {
            name: serie.name || '',
            className: serie.className || '',
            path: line( points ),
            points: points
          } )
        });
      // Define Axis
      const xAxis = {
        x1: this.margins[3],
        y1: scaleY(0),
        x2: this.state.size.w - this.margins[1],
        y2: scaleY(0)
      }
      // Lines and labels
      const lines = [];
      const labels = [];
      const bullets = [];
      let prevX = 0;
      this.props.Series[ this.props.Options.index ].timeline.forEach(
        ( day ) => {
          let x = scaleX( parseInt( day.timestamp, 10 ) );
          let y1 = this.margins[0];
          let y2 = this.state.size.h  - this.margins[2];
          if ( ( ( lines.length === 0 ) || ( Math.abs( x - prevX ) >= this.labelsDistance ) ) && ( x < ( this.state.size.w - this.labelsDistance ) ) && ( x >= this.labelsDistance ) ) {
            lines.push({
              x1: x,
              x2: x,
              y1: y1,
              y2: y2 + 10
            });
            bullets.push({
              cx: x,
              cy: y2 + 10,
              r: 2
            });
            labels.push({
              label: moment( day.date_short ).format("DD/MM"),
              x: x,
              y: y2 + 25
            });
            prevX = x;
          }
        });
      console.log( lines );
      this.setState( { loading: false, paths: { bullets, labels, lines, series, xAxis } } );
    }
	}

  viewbox() {
    let svgWrapper = this.chart.current;
    let size = {
      "h": svgWrapper.offsetHeight,
      "w": svgWrapper.offsetWidth,
    }
    this.setState(
      { size },
      () => {
        this.draw();
      }
    );
  }

  render() {
    console.log( this.state.paths.lines );
    const selectedIndex = this.props.Options.index || 0;
    return (
        <div className="LineChart" ref={this.chart}>
          <svg
            viewport={"0 0 " + this.state.size.w + ' ' + this.state.size.h}
            preserveAspectRatio="xMidYMid meet"
            width={this.state.size.w}
            height={this.state.size.h}>
            <g>
            {
                this.state.paths.lines.map(
                  (line, index) => {
                    return (
                      <line
                        key={index}
                        x1={line.x1}
                        y1={line.y1}
                        x2={line.x2}
                        y2={line.y2}
                        className="dateLine" />
                    );
                  }
                )
              }
            </g>
            <g>
              {
                this.state.paths.series.map(
                  (serie, index) => {
                    return (
                      <path
                        key={serie.className}
                        d={serie.path}
                        className={"line " + ( ( index === selectedIndex ) ? 'selected ' : '' ) + serie.className} />
                    );
                  }
                )
              }
            </g>
            <g>
              <line
                x1={this.state.paths.xAxis.x1}
                y1={this.state.paths.xAxis.y1}
                x2={this.state.paths.xAxis.x2}
                y2={this.state.paths.xAxis.y2}
                className="xAxis" />
            </g>
            <g>
            {
                this.state.paths.bullets.map(
                  (bullet, index) => {
                    return (
                      <circle
                        key={index}
                        cx={bullet.cx}
                        cy={bullet.cy}
                        r={bullet.r}
                        className="bullet" />
                    );
                  }
                )
              }
            </g>
            <g>
              {
                this.state.paths.labels.map(
                  (label, index) => {
                    return (
                      <text
                        key={index}
                        x={label.x}
                        y={label.y}
                        textAnchor="middle"
                        alignmentBaseline="top"
                        dominantBaseline="top"
                        className="label">{label.label}</text>
                    );
                  }
                )
              }
            </g>
          </svg>
        </div>
    );
  }
}

export default LineChart;
