import React, { Component } from 'react';
import './StreamGraph.css';
import * as d3 from 'd3';
import * as moment from 'moment';

class StreamGraph extends Component {
  constructor(props) {
    super(props);
    this.chart = React.createRef();

    this.draw = this.draw.bind( this );
    this.viewbox = this.viewbox.bind(this);

    this.labelsDistance = 50;

    this.state = {
      paths: {
        bullets: [],
        labels: [],
        lines: [],
        series: [],
      },
      show: 'numbers',
      size: {
        h: 0,
        w: 0
      }
    };
  }

  componentDidMount() {
    window.addEventListener( 'resize', this.viewbox, false );
    this.viewbox();
  }

  componentDidUpdate( pProps, pState ) {
    if ( pProps !== this.props ) {
      this.viewbox();
    }
  }
  
  componentWillUnmount() {
		window.removeEventListener( 'resize', this.viewbox, false );
	}

  draw() {
    if ( this.props.Data && this.props.Options.show && this.props.Options.show !== '' && this.props.Options.series && this.props.Options.series.length > 0 ) {
      // Scales
      const scaleX = d3.scaleLinear()
        .domain(d3.extent( this.props.Data[ this.props.Options.show ].map( (child) => +child.day )))
        .range([ 0, this.state.size.w ]);
      
      const scaleY = d3.scaleLinear()
        .domain( [ this.props.Options.max.y, ( -1 * this.props.Options.max.y ) ] )
        .range([ this.state.size.h, 0 ]);

      // Series
      const stackedData = d3.stack()
        .offset( d3.stackOffsetSilhouette )
        .keys( this.props.Options.series )( this.props.Data[ this.props.Options.show ] );      
      const area = d3.area()
        .x( point => scaleX( point.data.day ) )
        .y0( point => scaleY( point[0] ) )
        .y1( point => scaleY( point[1] ) )
        .curve( d3.curveBasis );      
      const series = [];
      this.props.Options.series.forEach(
        ( serie, index ) => {
          series.push( {
            name: serie,
            path: area( stackedData[index] )
          });
        }
      )

      // Days
      const centerY = this.state.size.h / 2;
      const labels = [];
      const lines = [];
      const bullets = [];
      const distance = 10;
      let prevX = 0;
      this.props.Data[ this.props.Options.show ].forEach(
        (entry) => {
          const x = scaleX( entry.day );
          if ( ( Math.abs( prevX - x ) >= this.labelsDistance ) && ( x <= ( this.state.size.w - this.labelsDistance ) ) ) {
            labels.push({
              label: moment( +entry.day * 1000 ).format("MMM Do"),
              x: x,
              y: centerY,
            });
            lines.push({
              x1: x,
              y1: 0,
              x2: x,
              y2: centerY - distance
            });
            lines.push({
              x1: x,
              y1: centerY + distance,
              x2: x,
              y2: this.state.size.h
            });
            bullets.push({
              x1: x - (distance / 2),
              y1: centerY + distance,
              x2: x + (distance / 2),
              y2: centerY + distance
            });
            bullets.push({
              x1: x - (distance / 2),
              y1: centerY - distance,
              x2: x + (distance / 2),
              y2: centerY - distance
            });
            bullets.push({
              x1: x - (distance / 2),
              y1: 0,
              x2: x + (distance / 2),
              y2: 0
            });
            bullets.push({
              x1: x - (distance / 2),
              y1: this.state.size.h,
              x2: x + (distance / 2),
              y2: this.state.size.h
            });
            prevX = x;
          }
        }
      );

      this.setState( { loading: false, paths: { series, lines, labels, bullets } } );
    }
	}

  viewbox() {
    let svgWrapper = this.chart.current;
    let size = {
      "h": svgWrapper.offsetHeight,
      "w": svgWrapper.offsetWidth,
    }
    this.setState(
      { size },
      () => {
        this.draw();
      }
    );
  }

  render() {
    return (
        <div className="StreamGraph" ref={this.chart}>
          <svg
            viewport={"0 0 " + this.state.size.w + ' ' + this.state.size.h}
            preserveAspectRatio="xMidYMid meet"
            width={this.state.size.w}
            height={this.state.size.h}>
            <g>
              {
                this.state.paths.series.map(
                  (serie, index) => {
                    return (
                      <path
                        key={serie.name}
                        d={serie.path}
                        className={"area " + serie.name} />
                    );
                  }
                )
              }
            </g>
            <g className="lines">
              {
                this.state.paths.lines.map(
                  (line, index) => {
                    return (
                      <line
                        key={index}
                        x1={line.x1}
                        y1={line.y1}
                        x2={line.x2}
                        y2={line.y2}
                        className="line" />
                    );
                  }
                )
              }
            </g>
            <g className="bullets">
              {
                this.state.paths.bullets.map(
                  (bullet, index) => {
                    return (
                      <line
                        key={index}
                        x1={bullet.x1}
                        y1={bullet.y1}
                        x2={bullet.x2}
                        y2={bullet.y2}
                        className="bullet" />
                    );
                  }
                )
              }
            </g>
            <g className="labels">
              {
                this.state.paths.labels.map(
                  (label, index) => {
                    return (
                      <text
                        key={index}
                        x={label.x}
                        y={label.y}
                        textAnchor="middle"
                        alignmentBaseline="middle"
                        dominantBaseline="middle"
                        className="label">{label.label}</text>
                    );
                  }
                )
              }
            </g>
          </svg>
        </div>
    );
  }
}

export default StreamGraph;
