import React, { Component } from 'react';
import axios from 'axios';

import AreaChart from '../AreaChart/AreaChart';

import './OverviewAreaChart.css';

class OverviewAreaChart extends Component {
    
  constructor( props ) {
    super(props);
      
    this.endpoint = 'https://india.elezioni.io/v0/stats/history';
    
    this.load = this.load.bind( this );
    
    this.state = {
      collections: {},
      loading: true,
      max: {}
    }
  }
  
  componentDidMount() {
    this.load();
  }
	
	load() {
	  axios.get( this.endpoint, { withCredentials: false } ).then(
	    ( response ) => {
	      if ( response.status === 200 && !response.data.error ) {
          this.setState(
	          {
              collections: response.data.data.collections,
              loading: false,
              max: response.data.data.max
            });
	      } else {
	        alert( 'An error occourred ' + response.statusText + ' ' + response.data.errormessage);
	      }
	    }
	  )
	  .catch(
	    ( error ) => {
	      alert( 'Error ' + error );
	      console.log( error );
	    }
	  );
	}
	
	
    
  render() {
    return (
      <div className="OverviewAreaChart">

      <h1>OverviewAreaChart Component</h1>

      <h3>Activity</h3>
       
       <figure className="OverviewAreaChart--chart">
        <div className="in">
          <div className="OverviewAreaChart--wrapper">
            <AreaChart Series={this.state.collections} Options={ { max: { y: this.state.max.activity }, scale: 'numbers', y: 'activity' } } />
          </div>
        </div>
       </figure>

      <hr />

      <h3>Activity %</h3>

       <figure className="OverviewAreaChart--chart">
        <div className="in">
          <div className="OverviewAreaChart--wrapper">
            <AreaChart Series={this.state.collections} Options={ { max: { y: 100 }, scale: 'perc', y: 'activity_perc' } } />
          </div>
        </div>
       </figure>

       <hr />

       <h3>Popularity</h3>
       
       <figure className="OverviewAreaChart--chart">
        <div className="in">
          <div className="OverviewAreaChart--wrapper">
            <AreaChart Series={this.state.collections} Options={ { max: { y: this.state.max.popularity }, scale: 'numbers', y: 'popularity' } } />
          </div>
        </div>
       </figure>

        <hr />

        <h3>Popularity %</h3>

        <figure className="OverviewAreaChart--chart">
        <div className="in">
          <div className="OverviewAreaChart--wrapper">
            <AreaChart Series={this.state.collections} Options={ { max: { y: 100 }, scale: 'perc', y: 'popularity_perc' } } />
          </div>
        </div>
        </figure>
        <h2>{ ( this.state.loading ? 'loading' : 'loaded' ) } - {this.state.loading}</h2>
      
      </div>
    );
  }
}

export default OverviewAreaChart;
