import React, { Component } from 'react';
import axios from 'axios';

import LineChart from '../LineChart/LineChart';

import './CompareCandidates.css';

class CompareCandidates extends Component {
    
  constructor( props ) {
    super(props);
      
    this.endpoint = 'https://india.elezioni.io/v0/stats/candidates';
    
    this.load = this.load.bind( this );
    
    this.state = {
      candidates: [],
      loading: true,
      max: {
        activity: 0,
        day: 0,
        popularity: 0,
      }
    }
  }
  
  componentDidMount() {
    this.load();
  }
	
	load() {
	  axios.get( this.endpoint, { withCredentials: false } ).then(
	    ( response ) => {
	      if ( response.status === 200 && !response.data.error ) {
          console.log(response.data);
          const max = {
            activity: Number.MIN_SAFE_INTEGER,
            popularity: Number.MIN_SAFE_INTEGER,
            day: Number.MIN_SAFE_INTEGER
          }

          // find max 
          response.data.data.forEach(
            ( candidate ) => {
              candidate.timeline.forEach(
                ( day ) => {
                  if ( day.timestamp > max.day ) {
                    max.day = day.timestamp;
                  }
                  if ( day.activity > max.activity ) {
                    max.activity = day.activity;
                  }
                  if ( day.popularity > max.popularity ) {
                    max.popularity = day.popularity;
                  }
                }
              );
            }
          );


	        this.setState(
	          {
              candidates: response.data.data,
              loading: false,
              max
	          }
	        );
	      } else {
	        alert( 'An error occourred ' + response.statusText + ' ' + response.data.errormessage);
	      }
	    }
	  )
	  .catch(
	    ( error ) => {
	      alert( 'Error ' + error );
	      console.log( error );
	    }
	  );
  }
  
  render() {
    let candidates = this.state.candidates;
    let max = this.state.max;
    return (
      <div className="CompareCandidates">
      {
        this.state.candidates.map(
          ( candidate, index ) => {
            let options = {
              index,
              max,
              show: 'activity'
            };
            return (
              <div
                key={candidate.name}
                className={"CompareCandidates--candidate " + candidate.className}>
                  <h2>{candidate.name}</h2>
                  <figure className="CompareCandidates--chart">
                    <LineChart Series={candidates} Options={options}/>
                  </figure>
              </div>
            )
          }
        )
      }
      </div>
    );
  }
}

export default CompareCandidates;
